#todo-banner{
    max-width:600px; 
}

.key {
  font-size:18px;
  }

  li.react-item {
    list-style-type:none;
    padding: 10px 16px;
    margin: 10px 0px;
    cursor:pointer;
    background-color: beige;  
    border-radius:9px;
    }

    li.react-item:before {
    content:url('../img/todo-bullet.png');
    margin-left: -45px;
    margin-right: 17px;
    display: inline-block;
    }
    li.react-item:hover:before {
    transform:rotate(360000deg);
    transition:300s;/* tune this */
    }

    li.react-item:hover{  
      background-color: #ffdab9;
    }

    .btn-demo{
      background-color: #6f6a91;
      color:white;
    }


      .btn-demo:hover{
      background-color:  #ea8b8b;
      color: white;
    }
  
  .small li{
    font-size:15px;
  }

  .hero {

    margin: 0px auto 3em auto;
    overflow: hidden; 
  }

  .hero--inner{
    position: relative;
  }

  .banner{
     height: auto;
  }

  .img {
